.nd_navbar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  padding: 15px;
  height: 60px;
  z-index: 10;
  max-width: 100vw;

  &_list {
    display: flex;
    list-style: none;
    margin: 0 auto;
    padding: 0;
    max-width: 768px;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  &_item {
    padding: 0;
    margin: 0;

    a {
      color: #333;

      &.active {
        color: #c6a734;
      }
    }
  }
}
