.wrapper {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../../static/images/gallery/11.jpg");
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 12;
  max-width: 768px;
}

.overlay {
  width: 100%;
  height: 100%;
  background: rgba($color: #000000, $alpha: 0.25);
}

.content {
  text-align: center;
  padding-top: 50px;

  h3 {
    font-family: "Open Sans", sans-serif;
    font-style: italic;
    font-weight: normal;
    font-size: 20px;
    line-height: 33px;
    text-align: center;
    letter-spacing: 0.1em;
    color: #ffffff;
    margin: 0;
  }

  button {
    border: none;
    padding: 10px 18px;
    text-transform: uppercase;
    border-radius: 100px;
    background: rgba(255, 255, 255, 0.7);

    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.1em;
    color: #000000;
  }
}
