.wrapper {
  display: block;
  background-color: #fff;
  padding: 10px;

  h1 {
    font-family: "Great Vibes", sans-serif;
    color: #c6a734;
    margin: 10px;
  }

  .form {
    text-align: center;
    min-height: 290px;
    max-height: 290px;
  }

  input,
  textarea {
    font-family: "Open Sans", sans-serif;
    border: 1px solid #eee;
    padding: 5px 10px;
    border-radius: 4px;
    width: 100%;
    margin-bottom: 10px;

    &:focus {
      outline: none;
      border: 1px solid #c6a734;
    }
  }

  button {
    font-family: "Open Sans", sans-serif;
    font-weight: bold;
    border: none;
    background-color: #c6a734;
    color: #fff;
    text-transform: uppercase;
    width: 100%;
    padding: 10px;
    border-radius: 4px;
    margin-bottom: 10px;

    &:disabled {
      background-color: #eee;
      color: #aaa;
    }
  }
}

.list {
  max-height: 100%;
  overflow-y: auto;
  max-height: 80vh;
  margin-top: 20px;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  ul,
  li {
    list-style: none;
    padding: 0;
  }
}

.chat {
  font-family: "Open Sans", sans-serif;
  padding: 10px;
  border-bottom: 1px dashed #eee;

  &_header {
    display: flex;
    align-items: center;
    justify-content: space-between;

    strong {
      font-family: "Dancing Script", cursive;
      font-weight: bold;
      color: #c6a734;
      font-size: 18px;
    }

    span {
      font-size: 10px;
      color: #bbb;
    }
  }

  &_body {
    font-size: 13px;
    line-height: 20px;
    color: #555;
    margin-top: 7px;
  }
}
