.wrapper {
  width: 100%;
  height: 100vh;
  position: relative;
  background-image: url("../../static/images/home.jpeg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  max-width: 100vw;
  text-align: center;
}

.home_content {
  position: absolute;
  top: 60px;
  left: 0;
  right: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.1em;
  color: #ffffff;

  i {
    font-style: italic;
    font-weight: 300;
  }

  h3 {
    font-style: normal;
    font-weight: normal;
  }
}

.overlay {
  position: absolute;
  width: 100%;
  height: 323px;
  left: 0px;
  top: 0px;

  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.8) 0%,
    rgba(0, 0, 0, 0.6) 51.04%,
    rgba(0, 0, 0, 0) 100%
  );
}
