.header {
  font-family: "Dancing Script", cursive;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
  color: #000000;
  max-width: max-content;
  margin: 40px auto;

  p {
    max-width: 330px;
    margin: 5px 0;

    span {
      color: #c6a734;
    }
  }
}

.wrapper {
  border-top: 10px solid white;
  background-color: #f5f5f5;
  min-height: 100vh;
  padding-bottom: 50px;
}
