.wrapper {
  background-color: white;
  text-align: center;
  padding-bottom: 50px;
  padding-top: 20px;
}

.header {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  font-family: "Dancing Script", cursive;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
}

.sections {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto;
  row-gap: 20px;
  margin-bottom: 20px;
}

.maps {
  max-width: calc(100vw - 20px);
  margin: 20px auto;
}
