.wrapper {
  border-radius: 4px;
  max-width: 100%;
  height: 570px;
  overflow: hidden;
}

.image {
  background-image: url("../../static/images/counter.jpg");
  background-position: center bottom;
  background-size: cover;
  background-repeat: no-repeat;
  min-width: 100%;
  min-height: 100%;
  padding-top: 10px;
}

.counter {
  padding: 8px;
  max-width: 90%;
  background: rgba($color: #fff, $alpha: 0.9);
  margin: 0 auto;
  border-radius: 4px;

  &_header {
    h1 {
      font-family: "Great Vibes", cursive;
      font-size: 24px;
      line-height: 30px;
      text-align: center;
      letter-spacing: 0.1em;
      color: #c6a734;
    }
  }
  &_content {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  &_item {
    & > div {
      background-color: black;
      max-width: 70px;
      min-width: 70px;
      padding: 6px;
      border-radius: 4px;
    }

    h1 {
      color: #fff;
      font-family: "Open Sans", sans-serif;
      font-weight: bold;
      font-size: 24px;
      line-height: 33px;
      margin: 0;
      text-align: center;
    }

    p {
      font-family: "Open Sans", sans-serif;
      font-weight: 800;
      font-size: 14px;
      line-height: 20px;
      text-align: center;
      letter-spacing: 0.1em;
      color: #000000;
    }
  }
}
