.wrapper {
  background-color: white;
  padding-top: 20px;
  padding-bottom: 50px;

  h1 {
    font-family: "Great Vibes", cursive;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    letter-spacing: 0.1em;
    color: #c6a734;
    margin: 0;
    padding-bottom: 20px;
  }

  img {
    max-width: 100%;
    border-radius: 4px;
  }

  .image {
    padding: 10px 10px 0;

    img {
      transition: ease-in-out 1s;
    }
  }

  .images {
    display: flex;
    align-items: center;
    flex-direction: row;
    max-width: 100%;
    overflow-y: auto;
    padding: 10px;
    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }

    img {
      max-width: 25%;
      margin-left: 10px;
      filter: grayscale(1);

      &:first-child {
        margin-left: 7px;
      }
    }
  }
}

.active {
  filter: grayscale(0) !important;
  // border: 2px solid #c6a734;
  box-shadow: 0 0 10px 2px #c6a734;
}
