.wrapper {
  display: flex;
  flex-direction: column;
  width: max-content;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  line-height: 25px;
  color: #000000;

  img {
    width: 230px;
    height: 230px;
  }

  p {
    margin: 0;
  }
}

.name {
  font-family: "Dancing Script", cursive;
  font-size: 22px;
  line-height: 29px;
  letter-spacing: 0.1em;
  color: #000000;

  padding-bottom: 7px;
  border-bottom: 2px solid #c6a734;
  width: max-content;
  margin-top: 12px;
  margin-bottom: 8px;
}
